.result {
	width: 30%;
	min-width: 220px;
	padding: 17px;
	display: flex;
	flex-direction: column;
	cursor: pointer;
}

.result img {
	display: block;
    width: 100%;
}

.result h3 {
	color: #FFF;
	font-size: 20px;
	font-weight: 600;
	padding: 10px;
	background: #445565;
	/* flex: 1 1 100%; */
	transition: 0.4s ease-out;
}

.result:hover h3 {
	background: #FFF;
	color: #223343;
}